/*---------------------------------------------------------------------------------
   Common Styles
---------------------------------------------------------------------------------*/
/*float styles*/
.clearfix:before,
.clearfix:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix:after {
  clear: both;
}

* {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  font: 110%/1 'Lato', Calibri, Arial, sans-serif;
  background-color: #394264;
}

body {
  width: 40rem;
  height: auto;
  padding: 0;
  margin: 3rem auto;
  text-align: center;
  line-height: 1.25;
  color: white;
  background-color: #1F253D;
  border: 2px solid #000000;
  border-radius: 10px;
  box-shadow: 25px 25px 25px 0px rgba(11,15,24,1);
}

ul, ol {
  list-style-type: none;
  padding-left: 0;
}

ul li {
  display: inline;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}



/*---------------------------------------------------------------------------------
   A11y Styles
---------------------------------------------------------------------------------*/

/* Hide only visually, but have it available for screenreaders: h5bp.com/v */
.visuallyhidden { 
    border: 0 !important; 
    clip: rect(0 0 0 0) !important; 
    height: 1px !important; 
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important; 
}

/* Extends the .visuallyhidden class to allow the element 
to be focusable when navigated to via the keyboard: h5bp.com/p */ 
.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
    clip: auto !important;
    height: auto !important;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto !important;
}

/*---------------------------------------------------------------------------------
  Animations for typography & form elements
---------------------------------------------------------------------------------*/

h1, h2, p, a, input, button, ol, ul {
  -webkit-transition: all .2s linear;
  transition: all .2s linear; 
}

/*---------------------------------------------------------------------------------
  Media queries
---------------------------------------------------------------------------------*/

@media screen and (min-width: 40rem) {
  html {
    font-size: 120%;
  }
  h1, h2, p, a, input, button, ol, ul {
    -webkit-transition: all .2s linear;
    transition: all .2s linear; 
  }
}

@media screen and (min-width: 51rem) {
  html {
    font-size: 125%;
    line-height: 1.5;
  }
  h1, h2, p, a, input, button, ol, ul {
    -webkit-transition: all .2s linear;
    transition: all .2s linear; 
  }
}
h1 {
  font-weight: 900;
  font-size: 3em;
  padding: 0.8em;
  color: #fff;
}

nav {
  position: relative;
  height: 10%;
  padding: 1em;
}

nav ul li {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2em;
}

nav ul li:first-child {
  float: left;
}

nav ul li {
  float: right;
}

nav a {
  color: #fff;
}

.status-link {
  z-index: 100;
  top: 60px;
}

/* Create a highlight keyframe */
@-webkit-keyframes feedback-highlight {
  0% {
    background: #9b1228;
  }
  100% {
    background: #cc324b;
  }
}
@keyframes feedback-highlight {
  0% {
    background: #9b1228;
  }
  100% {
    background: #cc324b;
  }
}

#feedback {
	margin: 0 auto;
  background: #cc324b;
  padding: 1em 0.4em;
  font-size: 1.5em;
  font-weight: 400;
  display: block;
  line-height: 1em;
  color: #fff;
}

#feedback:target {
  -webkit-animation: feedback-highlight 3s;
          animation: feedback-highlight 3s;
}

input, button {
  width: 18.75rem;
  height: 3.125rem;
  line-height: 100%;
  display: block;
  padding: 0.8em 0;
  margin: 0.8rem auto 0;
  background: #50597b;
  color: #fff;
  border: solid 1px #1f253d;
  border-radius: 4px;
}

button.button {
  color: #95a5a6;
  font-size: 2em;
  padding: 0;
  border: 3px solid grey;
  -webkit-transition: background 1s ease-in-out;
  transition: background 1s ease-in-out;
}

button.button:hover,
button.button:focus {
  background: #e64c65;
  color: #fff;
  cursor: pointer;
}

input.text {
  text-align: center;
  padding: 0.2em;
